body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.imgg{
  height: 9rem;

}
.navbar-bg{
  background-color:#f1f5fd;
  background-color:var(--bgcolor);
  padding: 2rem 0;
  z-index: 99;
  position: relative;
  
}



.navbar-brand{
font-size: 2.5rem;
}
.navbar li{
  padding: 0 2rem;
}
.navbar li a{
  font-size: 1.7rem;
  font-weight: lighter;
}
.navbar li a:hover{
  color:#333333!important;
  color:var(--main-text-color)!important;
  text-shadow: 0 0.2rem 0 rgba(183, 171, 255, 0.5)
}

.btn-container{
  display: flex;
  grid-template-rows: 1fr 1fr 1fr 1fr; 
}
.btn-style{
  width: 10rem;
  height:2.1rem;
border-radius: 2rem;
text-transform: capitalize;
margin-right: 2rem;
font-size: 1.0rem;
background-color:  #6f5de7;
background-color:  var(--primary-color);
color: #fff;
}
.btn-style:hover{
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}
.btn-style-border{
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}
.btn-style-border:hover{
text-transform: capitalize;
background-color:#6f5de7;
background-color:var(--primary-color);
color: #fff;
}

 .menu-item{ 
 display: grid;
 grid-template-columns: 1fr 1fr 1fr 1fr ;
  grid-gap: 0 1.25rem;
  gap: 0 1.25rem;
  max-width: 40rem;
}
.menu-photo{
  width: 20rem;
  height: 20rem;
  padding: 2rem 0;
}

.btn-style-color{
  width: 10rem;
  height:2.1rem;
border-radius: 2rem;
text-transform: capitalize;
margin-right: 2rem;
font-size: 1.1rem;
background-color:#ee8e11;
background-color:var(--light-color);
color: #fff;

}
.btn-style-color:hover{
  text-transform: capitalize;
background-color:#f1f5fd;
background-color:var(--bgcolor);
color: #074b8f;
color: var(--light-bg);
box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}
.btn-style-color:active{
  background-color:#125f03;
  background-color:var(--light-green);
color: #f1f5fd;
color: var(--bgcolor);
box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}
.btn-style-bg{
  width: 5rem;
  height:2.1rem;
text-transform: capitalize;
margin-right: 1rem;
font-size: 1.2rem;
background-color:  #074b8f;
background-color:  var(--light-bg);
color: #fff;
}
@media screen and (min-width: 1200px){
  .section-center{
    width:95vw;
    display:grid;
    grid-template-columns: 1fr  1fr ;
    grid-template-rows: 1fr  1fr 1fr 1fr;
    padding: 5rem 5rem 5rem 5rem;
  } 
}

@media (min-width:1500px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl{
    max-width: 1500px !important;
  }

}


@media (min-width:1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl{
    max-width: 1400px !important;
  }

}

:root{
  --bgcolor:#f1f5fd;
  --primary-color:#6f5de7;
  --text-color:#828286;
  --main-text-color:#333333;
  --light-color:#ee8e11;
  --light-bg:#074b8f;
  --light-green:#125f03;

  
}


